.NeighborMineCount {
  font-family: sans-serif;
}

.NeighborMineCount--1 {
  color: #00f;
}

.NeighborMineCount--2 {
  color: #007b00;
}

.NeighborMineCount--3 {
  color: #f00;
}

.NeighborMineCount--4 {
  color: #00007b;
}

.NeighborMineCount--5 {
  color: #7b0000;
}

.NeighborMineCount--6 {
  color: #006968;
}

.NeighborMineCount--7 {
  color: #007b7b;
}

.NeighborMineCount--8 {
  color: #7b7b7b;
}

.GameCellOpened {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: default;
}

.GameCellUnopened {
  width: 100%;
  height: 100%;
  border: 2px outset #ccc;
  background-color: #ddd;
  cursor: pointer;
}

.GameCellUnopened:hover {
  background-color: #eee;
}

.GameCellUnopened:focus {
  outline: none;
  z-index: 2;
}

.GameCellUnopened:active {
  border: 2px inset #ccc;
  background-color: #eee;
}

.GameCell {
  width: 100%;
  height: 100%;
  position: relative;
  margin-right: -1px;
  margin-bottom: -1px;
  background-color: #fff;
  border: 1px solid #ccc;
}

.GameBoard {
  background-color: #fff;
  display: grid;
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.GameBoard--is-game-over {
  pointer-events: none;
}

.GameStatus {
  background: #fff;
}

.GameStatus p {
  margin: 0;
  padding: 1em;
}

.Game-board {
  background-color: #fff;
  padding: 1em;
}

.App {
  align-items: center;
}

html,
body,
#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: sans-serif;
  touch-action: manipulation;
  background-color: hsl(200, 80%, 90%);
}

